// ÂàùÂßãÂåñÈ°πÁõÆÈÄâÊã©ÂäüËÉΩ
function initProjectSelection() {
  const projectCards = document.querySelectorAll('.project-card');
  const backToProjectsBtn = document.getElementById('back-to-projects-btn');
  
  // ‰∏∫ÊØè‰∏™È°πÁõÆÂç°ÁâáÊ∑ªÂä†ÁÇπÂáª‰∫ã‰ª∂
  projectCards.forEach(card => {
    card.addEventListener('click', () => {
      const projectId = card.getAttribute('data-project');
      selectProject(projectId);
    });
  });
  
  // ËøîÂõûÈ°πÁõÆÈÄâÊã©ÊåâÈíÆ‰∫ã‰ª∂
  if (backToProjectsBtn) {
    backToProjectsBtn.addEventListener('click', () => {
      showProjectSelection();
    });
  }
}

// ÈÄâÊã©È°πÁõÆ
function selectProject(projectId) {
  if (!PROJECT_CONFIG[projectId]) {
    console.error('Unknown project:', projectId);
    return;
  }
  
  currentProject = projectId;
  const projectConfig = PROJECT_CONFIG[projectId];
  
  // Êõ¥Êñ∞ÂΩìÂâçÈ°πÁõÆ‰ø°ÊÅØ
  const titleElement = document.getElementById('current-project-title');
  const descElement = document.getElementById('current-project-desc');
  
  if (titleElement) {
    titleElement.textContent = projectConfig.icon + ' ' + projectConfig.name;
  }
  if (descElement) {
    descElement.textContent = projectConfig.description;
  }
  
  // ÊòæÁ§∫‰∏ä‰º†ÁïåÈù¢
  showUploadSection();
  
  // ÊòæÁ§∫Áä∂ÊÄÅÊ∂àÊÅØ
  showStatusMessage(`Â∑≤ÈÄâÊã©È°πÁõÆ: ${projectConfig.name}`, 'info');
}

// ÊòæÁ§∫È°πÁõÆÈÄâÊã©ÁïåÈù¢
function showProjectSelection() {
  const projectSelection = document.getElementById('project-selection-container');
  const uploadSection = document.getElementById('upload-section-container');
  
  if (projectSelection) {
    projectSelection.style.display = 'block';
  }
  if (uploadSection) {
    uploadSection.style.display = 'none';
  }
  
  // Ê∏ÖÈô§ÂΩìÂâçÈ°πÁõÆ
  currentProject = null;
  
  // Ê∏ÖÈô§‰∏ä‰º†ÁöÑÊñá‰ª∂
  if (uploadedProtocol) {
    removeProtocolFile();
  }
}

// ÊòæÁ§∫‰∏ä‰º†ÁïåÈù¢
function showUploadSection() {
  const projectSelection = document.getElementById('project-selection-container');
  const uploadSection = document.getElementById('upload-section-container');
  
  if (projectSelection) {
    projectSelection.style.display = 'none';
  }
  if (uploadSection) {
    uploadSection.style.display = 'block';
  }
}

// ÂàùÂßãÂåñÊñá‰ª∂‰∏ä‰º†ÂäüËÉΩ
function initFileUpload() {
  // Protocol upload
  const protocolSelectBtn = document.getElementById('protocol-select-btn');
  const protocolFileInput = document.getElementById('protocol-file-input');
  const protocolUploadArea = document.getElementById('protocol-upload-area');
  const protocolCancelBtn = document.getElementById('protocol-cancel-btn');
  const protocolRemoveBtn = document.getElementById('protocol-remove-btn');



  // Protocol upload events
  protocolSelectBtn.addEventListener('click', () => protocolFileInput.click());
  protocolUploadArea.addEventListener('click', () => protocolFileInput.click());
  protocolFileInput.addEventListener('change', (e) => handleProtocolUpload(e.target.files[0]));
  protocolCancelBtn.addEventListener('click', cancelProtocolUpload);
  protocolRemoveBtn.addEventListener('click', removeProtocolFile);

  // Drag and drop for protocol
  protocolUploadArea.addEventListener('dragover', handleDragOver);
  protocolUploadArea.addEventListener('drop', (e) => handleProtocolDrop(e));
  protocolUploadArea.addEventListener('dragenter', handleDragEnter);
  protocolUploadArea.addEventListener('dragleave', handleDragLeave);


}

// ÊãñÊãΩÂ§ÑÁêÜÂáΩÊï∞
function handleDragOver(e) {
  e.preventDefault();
  e.dataTransfer.dropEffect = 'copy';
}

function handleDragEnter(e) {
  e.preventDefault();
  e.currentTarget.classList.add('dragover');
}

function handleDragLeave(e) {
  e.preventDefault();
  e.currentTarget.classList.remove('dragover');
}

function handleProtocolDrop(e) {
  e.preventDefault();
  e.currentTarget.classList.remove('dragover');
  const files = e.dataTransfer.files;
  if (files.length > 0) {
    handleProtocolUpload(files[0]);
  }
}



// ProtocolÊñá‰ª∂‰∏ä‰º†Â§ÑÁêÜ
async function handleProtocolUpload(file) {
  if (!file) return;

  // È™åËØÅÊñá‰ª∂Á±ªÂûã
  const allowedTypes = [
    'application/pdf',
    'application/msword',
    'application/vnd.openxmlformats-officedocument.wordprocessingml.document'
  ];
  
  if (!allowedTypes.includes(file.type)) {
    showStatusMessage('ËØ∑Âè™ÈÄâÊã©PDFÊàñWordÊñáÊ°£', 'error');
    return;
  }

  // ÊòæÁ§∫‰∏ä‰º†ËøõÂ∫¶
  showProtocolProgress();
  
  try {
    // ÂàõÂª∫FormData
    const formData = new FormData();
    formData.append('document', file);
    formData.append('documentType', 'ClinicalProtocol'); // ÊòéÁ°ÆÊ†áËØÜ‰∏∫Clinical Protocol
    
    // Ê∑ªÂä†È°πÁõÆ‰ø°ÊÅØ
    if (currentProject) {
      formData.append('projectType', currentProject);
      formData.append('projectName', PROJECT_CONFIG[currentProject].name);
    }

    // ‰∏ä‰º†Êñá‰ª∂
    const response = await fetch(`${API_BASE_URL}/api/analyze-document`, {
      method: 'POST',
      body: formData
    });

    if (!response.ok) {
      throw new Error(`Upload failed: ${response.statusText}`);
    }

    const result = await response.json();
    
    // ‰øùÂ≠òÊñá‰ª∂‰ø°ÊÅØ
    uploadedProtocol = {
      name: file.name,
      size: file.size,
      type: file.type,
      uploadId: result.uploadId
    };

    // ÊòæÁ§∫‰∏ä‰º†ÁªìÊûú
    showProtocolResult(file);
    const projectName = currentProject ? PROJECT_CONFIG[currentProject].name : 'Êú™Áü•È°πÁõÆ';
    showStatusMessage(`ÊñáÊ°£Â∑≤ÊàêÂäü‰∏ä‰º†Âà∞È°πÁõÆ: ${projectName}`, 'success');
    
  } catch (error) {
    console.error('Protocol upload error:', error);
    showStatusMessage(`Upload failed: ${error.message}`, 'error');
    hideProtocolProgress();
  }
}



// UIÊõ¥Êñ∞ÂáΩÊï∞
function showProtocolProgress() {
  document.getElementById('protocol-upload-area').style.display = 'none';
  document.getElementById('protocol-progress').style.display = 'block';
  document.getElementById('protocol-result').style.display = 'none';
}

function hideProtocolProgress() {
  document.getElementById('protocol-upload-area').style.display = 'block';
  document.getElementById('protocol-progress').style.display = 'none';
}

function showProtocolResult(file) {
  document.getElementById('protocol-upload-area').style.display = 'none';
  document.getElementById('protocol-progress').style.display = 'none';
  document.getElementById('protocol-result').style.display = 'block';
  
  document.getElementById('protocol-file-name').textContent = file.name;
  const projectName = currentProject ? PROJECT_CONFIG[currentProject].name : 'Êú™Áü•È°πÁõÆ';
  document.getElementById('protocol-file-status').textContent = `‚úÖ Â∑≤‰∏ä‰º†Âà∞È°πÁõÆ: ${projectName}`;
}

function cancelProtocolUpload() {
  hideProtocolProgress();
  showStatusMessage('ÊñáÊ°£‰∏ä‰º†Â∑≤ÂèñÊ∂à', 'info');
  }

function removeProtocolFile() {
  uploadedProtocol = null;
  document.getElementById('protocol-upload-area').style.display = 'block';
  document.getElementById('protocol-result').style.display = 'none';
  document.getElementById('protocol-file-input').value = '';
  showStatusMessage('ÊñáÊ°£Â∑≤ÁßªÈô§', 'info');
}





// Â∑•ÂÖ∑ÂáΩÊï∞
function formatFileSize(bytes) {
  if (bytes === 0) return '0 Bytes';
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
}

function showStatusMessage(message, type) {
  const statusElement = document.getElementById('status-message');
  statusElement.textContent = message;
  statusElement.className = `status-message ${type}`;
  
  if (type === 'success' || type === 'info') {
    setTimeout(() => {
  statusElement.className = 'status-message';
    }, 3000);
  }
}


